/*****************************************
Colors:
- Primary - #14bf98
- Secondary - #1d1e1b
- Backgrounds - light gray #fbfbfb
- Background - dark blue #153e52
- Backgrounds navbar, footer - dark gray #1d1e1b
- Buttons, bullets, icons - green $primary
- Headings text - black #484a46
- Body text - gray #787976
- Body text - light gray #dfe5ec
******************************************/

:root {
    // --primary: #14bf98;
	--primary: #82c496;
	--secondary: #1d1e1b;
	--tertiarity: #fff;
